import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { StaffService } from 'src/app/api/controllers/Staff';
import { SharedService } from 'src/app/shared/services/shared.service';
import { data } from 'jquery';
import { ModalService } from 'src/app/shared/services/modal.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-staff-hierarchy',
  templateUrl: './staff-hierarchy.component.html',
  styleUrls: ['./staff-hierarchy.component.scss'],
  encapsulation  : ViewEncapsulation.None,
})

export class StaffHierarchyComponent implements OnInit {
  isDataLoaded:boolean = false;
  staffsList:any = [];
  StaffSetting:any = {
    singleSelection: false,
    primaryKey: 'staffId',
    labelKey: 'staffName',
    enableSearchFilter: true,
    enableFilterSelectAll: false,
    autoPosition: false,
    maxHeight:240
  }
  staffHierarchyList:any = [];
  apiSubscibe:Subscription;

  constructor(
    private sessionService:SessionService,
    private sharedService:SharedService,
    private modalService:ModalService,
    private staffService: StaffService,
  ) { }

  setStaffManager(data,event) {
    data.staffManagerId = event[0].staffId;
  }

  deSelectPrimaryStaff(data) {
    data.staffReportingList = [];
  }

  addHierarchy() {
    let params = {
      "staffManagerId": null,
      "staffReportingList": [],
      "isNew": true,
    }
    this.staffHierarchyList.push(params);
  }

  deleteStaffHierarchy(item) {
    this.modalService.showConfirmModal(item.staffManagerId);
  }


  addStaffHierarchy(data) {
    this.isDataLoaded = true;
    let params = {
      staffHierarchy : {
        "apartmentId": this.sessionService.apartmentId,
        "staffManagerId": data.staffManagerId,
        "isActive": true,
        "isNew" : data.isNew,
        "insertedBy": this.sessionService.userId,
        "insertedOn": new Date().toISOString(),
        "updatedBy": this.sessionService.userId,
        "updatedOn": new Date().toISOString(),
        "staffReportinglist": data.staffReportingList.map((staff)=> {return {staffId:staff.staffId}})
      }
    }
    this.staffService.upsertStaffHierarchy(params).subscribe((res:any) => {
      if(res.message) {
        this.isDataLoaded = false;
        this.getHierarchyList();
        this.sharedService.openSnackBar('Staff Hierarchy Created Successfully','success');
      } else {
        this.isDataLoaded = false;
        this.sharedService.openSnackBar(res.errorMessage,'error');
      }
    },(error)=>{
      this.isDataLoaded = false;
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  getHierarchyList() {
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.staffService.getAllStaffHierarchybyApartmentId(params).subscribe((res:any) => {
      if(Array.isArray(res)) {
        res.forEach((data) => {
          data.staffReportingList = data.staffReportingList.map((staff)=>{
            return ({
              'staffId' : staff.staffReportingId,
              'staffName' : staff.staffReportingName
            })
          })
        })
        this.staffHierarchyList = res;
      } else {
        this.sharedService.openSnackBar(res.errorMessage,'error');
      }
      this.isDataLoaded = false;
    },(error)=>{
      this.isDataLoaded = false;
      this.sharedService.openSnackBar('Server Error','error');
    });
  }



  ngOnInit(): void {
    
    // Staff List
    let staffParms = {
      apartmentId: this.sessionService.apartmentId,
      roleTypeIds: '1,3'  //Admin and Staff Id
    }
    this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
      this.staffsList = res;
    });

    this.getHierarchyList();

    //delete item
		this.apiSubscibe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null) {
        this.isDataLoaded = true;
        const queryParamBase = {
          staffManagerId: item,
          deleteBy: this.sessionService.userId,
        };
        this.staffService.deleteStaffHierarchyByStaffManager(queryParamBase).subscribe((res:any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          if(res.message) {
            this.isDataLoaded = false;
            this.getHierarchyList();
            this.sharedService.openSnackBar('Staff Hierarchy Deleted Successfully','success');
          } else {
            this.isDataLoaded = false;
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        },(error)=>{
          this.isDataLoaded = false;
          this.sharedService.setUnitListDeleteIndex(null);
          this.sharedService.openSnackBar('Server Error','error');
        });
			}
		});
  }

  ngOnDestroy(){
		this.apiSubscibe.unsubscribe();
	}

}
